<template>
  <div class="login-wrap common-wrap">
    <my-header type></my-header>
    <div class="login-content">
      <my-scroll>
        <img src="../assets/images/login_bgi.png" alt="" class="login-bgi">
        <div style="height: 178.5px"></div>
        <div class="login-form">
          <div class="login-form-item">
            <div class="login-input">
              <div class="icon-item icon-mobile"></div>
              <input type="text" placeholder="请输入手机号" v-model="phone" maxlength="11" oninput="value=value.replace(/[^\d]/g,'')">
            </div>
          </div>
          <div class="login-form-item">
            <div class="login-input">
              <div class="icon-item icon-safety"></div>
              <input type="text" placeholder="请输入验证码" v-model="code">
            </div>
            <div class="login-btn-inline login-btn" :class="{disable: time !== 0}" @click.stop.prevent="_sendCode" v-text="time === 0 ? '发送验证码' : time + '秒'"></div>
          </div>
          <div class="login-btn" @click.prevent.stop="_doLogin">登录</div>
        </div>
      </my-scroll>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login",
    components: {
      MyHeader: () => import('@/components/MyHeader'),
      MyScroll: () => import('@/components/MyScroll')
    },
    data() {
      return {
        phone: '15913656060',
        code: '1234',
        send: false,
        time: 0
      }
    },
    created() {
      window.sessionStorage.removeItem('token')
    },
    methods: {
      _sendCode() {
        this.send = true
        if (this.time === 0) {
          this.time = 60
          let codeInterval = setInterval(() => {
            if (this.time === 0) {
              clearInterval(codeInterval)
            } else {
              this.time--
            }
          }, 1000)
        } else {
          this.$toast.fail('请勿频繁操作')
        }
      },
      _doLogin() {
        if (this.phone.length !== 11) {
          if (this.phone.length === 0) {
            this.$toast.fail('请输入手机号')
          } else {
            this.$toast.fail('手机号格式有误')
          }
          return
        }
        if (!this.send) {
          this.$toast.fail('请先获取验证码')
          return
        }
        if (!this.code) {
          this.$toast.fail('请输入验证码')
          return
        }

        window.sessionStorage.setItem('token','test')
        this.$router.push('/index')
      }
    }
  }
</script>

<style scoped lang="less">
  .login-wrap{
    display: flex;
    flex-direction: column;
    .login-content{
      flex: 1;
      position: relative;
      overflow: hidden;
      .login-bgi{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
      }
      .login-form{
        position: relative;
        z-index: 1;
        background-color: #fff;
        margin: 0 29px;
        padding: 24px 18px;
        border-radius:8px;
        .login-form-item{
          display: flex;
          width: 100%;
          align-items: center;
          margin-bottom: 20px;
          .login-input{
            position: relative;
            flex: 1;
            >input{
              width: 100%;
              border: 0;
              outline: 0;
              background-color: #F7F7FC;
              line-height: 44px;
              border-radius: 2px;
              padding: 0 20px 0 52px;
              box-sizing: border-box;
            }
          }
        }
        .login-btn{
          height: 44px;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #3F9FFE;
          color: #fff;
          border-radius: 3px;
          &.login-btn-inline{
            width: 107px;
            margin-left: 8px;
          }
          &.disable{
            background-color: #cdcdcd;
          }
        }
      }
    }
  }
  .icon-item{
    width: 16px;
    height: 20px;
    background-repeat: no-repeat;
    background-position: center center;
    position: absolute;
    left: 19px;
    top: 12px;
    &.icon-mobile{
      background-image: url("../assets/images/mobile.png");
      background-size: 13px auto;
    }
    &.icon-safety{
      background-image: url("../assets/images/safety.png");
      background-size: 15px auto;
    }
  }
</style>
